.page{
    width: 100%;
    height: 100%;
    position: absolute;
}
.top-tags{
    bottom: auto;
    top: 35px;
}
.mid-tags{
    bottom: auto;
    top: 65px;
}
.tags{
    color: #ff00ff;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 28px;
    font-family: 'La Belle Aurore';
    user-select: none;
}
.bottom-tag-html{
    margin-left: -30px;
}
.bottom-tags{
    margin-bottom: 35px
}
.container{
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}
